import { EditDiagram } from "diagram/model/edit/edit";
import { emptyDiagram } from "diagram/state/state";
import { getStringId } from "utilities/id";
import { Library, LibraryItem } from "./library";

type Reducer = (library: Library) => Library;

const add = (): Reducer => (prev) => {
    const names = prev.all.map((item) => item.name);
    const item: LibraryItem = {
        name: getStringId(names, "Untitled"),
        diagram: emptyDiagram,
        saved: false,
    };
    return {
        current: item.name,
        all: prev.all.concat(item),
    };
};

const set = (target: string, diagram: EditDiagram): Reducer => (prev) => {
    let changed = false;
    const all = prev.all.map((item) => {
        if (item.name !== target) return item;
        if (item.diagram === diagram) return item;
        changed = true;
        return { ...item, diagram };
    });
    if (changed === false) return prev;
    return { ...prev, all };
};

const save = (target: string): Reducer => (prev) => {
    let changed = false;
    const all = prev.all.map((item) => {
        if (item.name !== target) return item;
        if (item.saved === true) return item;
        changed = true;
        return { ...item, saved: true };
    });
    if (changed === false) return prev;
    return { ...prev, all };
};

const remove = (target: string): Reducer => (prev) => {
    const current = prev.current === target ? null : prev.current;
    const all = prev.all.filter((item) => item.name !== target);
    return { current, all };
};

const rename = (from: string, to: string): Reducer => (prev) => {
    const current = prev.current === from ? to : prev.current;
    const all = prev.all.map((item) => {
        if (item.name !== from) return item;
        return { ...item, name: to, saved: true };
    });
    return { current, all };
};

const open = (name: string): Reducer => (prev) => ({
    current: name,
    all: prev.all,
});

const duplicate = (from: string, to: string): Reducer => (prev) => {
    const prevItem = prev.all.find((item) => item.name === from);
    if (prevItem === undefined) throw Error("Item is undefined");
    return {
        current: to,
        all: prev.all.concat({ ...prevItem, name: to }),
    };
};

export const LibraryReducer = {
    remove,
    rename,
    save,
    open,
    add,
    duplicate,
    set,
};
