import { Button, DivPx, Paragraph } from "@moai/core";
import { isAppHost } from "host/host";
import * as React from "react";
import { Report } from "report/report";

interface Props {
    children: React.ReactChild;
}

interface State {
    hasError: boolean;
    error: any;
}

const Description = () => (
    <>
        <Paragraph>
            Please tell us about this problem. We will treat your report as
            confidential and anonymous.
        </Paragraph>
        <DivPx size={8} />
        <Paragraph>
            To help us diagnose the problem, please describe what you were doing
            when this error occured:
        </Paragraph>
        <DivPx size={8} />
    </>
);

const Footer = () => {
    if (isAppHost) return null;
    const reload = () => window.location.reload();
    return <Button onClick={reload} children="Reload" />;
};

export class ErrorBoundary extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = { hasError: false, error: null };
    }

    static getDerivedStateFromError(error: any) {
        return { hasError: true, error };
    }

    render() {
        if (this.state.hasError)
            return (
                <Report
                    type="uncaught"
                    title="There was an unexpected error"
                    description={<Description />}
                    footer={<Footer />}
                    error={this.state.error}
                />
            );
        return this.props.children;
    }
}
