import { Switcher, SwitcherOption } from "@moai/core";
import { icons } from "@moai/icon";
import { PreferencesContext } from "preferences/preferences";
import { Theme } from "preferences/theme";
import * as React from "react";
import { headerButtonSize } from "../header";

const options: SwitcherOption<Theme>[] = [
    { value: "light", label: "Light", icon: icons.flash, key: "light" },
    { value: "auto", label: "System", key: "auto" },
    { value: "dark", label: "Dark", icon: icons.moon, key: "dark" },
];

export const HeaderTheme = () => {
    const context = React.useContext(PreferencesContext);
    const { preferences: prefs, setPreferences: setPrefs } = context;
    return (
        <Switcher<Theme>
            value={prefs.theme}
            setValue={(theme) => setPrefs((prev) => ({ ...prev, theme }))}
            options={options}
            size={headerButtonSize}
        />
    );
};
