import { showAlert } from "@moai/core";
import * as React from "react";
import { isAppHost } from "./host";

const Instruction = () => (
    <span>
        <span>Please download </span>
        <a href="https://tableplus.com" children="TablePlus" />
        <span> app and install the Diagram plugin </span>
        <span>(Cmd + L on Mac or Ctrl + L on Windows)</span>
    </span>
);

export const showInstallHostAlert = (message: React.ReactNode) => {
    if (isAppHost) throw Error("Already inside an AppHost");
    showAlert([message, <Instruction />]);
};
