import { Button, DivPx, Paragraph } from "@moai/core";
import { icons } from "@moai/icon";
import * as React from "react";
import { Report } from "report/report";
import { headerButtonSize } from "../header";

const Description = () => (
    <>
        <Paragraph>
            Thank you for helping us improve the app. We will treat your report
            as confidential and anonymous.
        </Paragraph>
        <DivPx size={8} />
        <Paragraph>
            Please describe the issue, including any steps to reproduce it:
        </Paragraph>
        <DivPx size={8} />
    </>
);

const Footer = (props: { cancel: () => void }) => (
    <Button onClick={() => props.cancel()} children="Cancel" />
);

export const HeaderReport = () => {
    const [visible, setVisible] = React.useState(false);
    const close = () => setVisible(false);
    return (
        <>
            <Button
                onClick={() => setVisible(true)}
                selected={visible}
                size={headerButtonSize}
                icon={icons.issue}
                children="Report issue"
            />
            {visible && (
                <Report
                    type="manual"
                    title="Report an Issue"
                    description={<Description />}
                    footer={<Footer cancel={close} />}
                    onDone={close}
                />
            )}
        </>
    );
};
