import { parseDiagram } from "diagram/model/parse/parse";
import { Library } from "library/library";

const DOC_PREFIX = "dg/doc/";

const getKey = (name: string): string => `${DOC_PREFIX}${name}`;

const get = async (name: string): Promise<string> => {
    const stored = window.localStorage.getItem(getKey(name));
    if (stored !== null) return stored;
    throw Error(`Document not found: "${name}"`);
};

const set = async (name: string, value: string): Promise<void> => {
    window.localStorage.setItem(getKey(name), value);
};

const list = async (): Promise<Library["all"]> => {
    return Object.entries(window.localStorage)
        .map((entry) => {
            return { key: entry[0], value: entry[1] as string };
        })
        .filter((entry) => entry.key.startsWith(DOC_PREFIX))
        .map((entry) => ({
            name: entry.key.replace(DOC_PREFIX, ""),
            diagram: parseDiagram(entry.value),
            saved: true,
        }));
};

const remove = async (name: string): Promise<void> => {
    window.localStorage.removeItem(getKey(name));
};

const duplicate = async (from: string, to: string): Promise<void> => {
    const value = await get(from);
    await set(to, value);
};

const rename = async (from: string, to: string): Promise<void> => {
    await duplicate(from, to);
    await remove(from);
};

export const DocStorage = {
    get,
    set,
    list,
    remove,
    duplicate,
    rename,
};
