import { Button, ButtonGroup } from "@moai/core";
import { icons } from "@moai/icon";
import { DiagramHistoryProps } from "diagram/state/state";
import * as React from "react";
import { headerButtonSize } from "../header";

interface Props extends DiagramHistoryProps {}

export const HeaderHistory = (props: Props) => (
    <ButtonGroup>
        {[
            <Button
                key="undo"
                onClick={() => props.diagramHistoryDispatch({ type: "undo" })}
                icon={icons.undo}
                size={headerButtonSize}
                disabled={props.diagramHistory.past.length === 0}
                title="Undo"
            />,
            <Button
                key="redo"
                onClick={() => props.diagramHistoryDispatch({ type: "redo" })}
                icon={icons.redo}
                size={headerButtonSize}
                disabled={props.diagramHistory.future.length === 0}
                title="Redo"
            />,
        ]}
    </ButtonGroup>
);
