import { background, Border, Button, DivGrow, Input, Select } from "@moai/core";
import { DiagramElements } from "canvas/elements";
import { DiagramHistoryProps } from "diagram/state/state";
import { host, isAppHost } from "host/host";
import * as React from "react";
import { HeaderAnimation } from "./animation/animation";
import { HeaderArrange } from "./arrange/arrange";
import { HeaderExport } from "./export/export";
import { HeaderFooter } from "./footer/footer";
import s from "./header.module.css";
import { HeaderHistory } from "./history/history";
import { HeaderImport } from "./import/import";
import { HeaderLibrary } from "./library/library";
import { HeaderMore } from "./more/more";
import { HeaderReport } from "./report/report";
import { HeaderSearch } from "./search/search";
import sBackground from "./styles/background.module.scss";
import { headerHostStyle } from "./styles/host";
import { HeaderTable } from "./table/table";
import { HeaderTheme } from "./theme/theme";
import { HeaderZoom } from "./zoom/zoom";

export const headerButtonSize =
    host === "mac" ? Button.size.small : Button.size.medium;
export const headerInputSize =
    host === "mac" ? Input.size.small : Input.size.medium;
export const headerSelectSize =
    host === "mac" ? Select.size.small : Select.size.medium;

const sBox = [s.box, headerHostStyle.box].join(" ");
const sContainer = [s.container, headerHostStyle.container].join(" ");
const sMain = [s.main, headerHostStyle.main].join(" ");
const sMore = [s.more, headerHostStyle.more].join(" ");

const EditControls = (props: Props) => (
    <>
        <div className={[sBox, s.boxEdit].join(" ")}>
            <HeaderImport
                elements={props.elements}
                diagramDispatch={props.diagramHistoryDispatch}
            />
        </div>
        <div className={[sBox, s.boxEdit].join(" ")}>
            <HeaderTable
                diagram={props.diagramHistory.present}
                diagramDispatch={props.diagramHistoryDispatch}
                elements={props.elements}
            />
        </div>
        <div className={[sBox, s.boxEdit].join(" ")}>
            <HeaderHistory
                diagramHistory={props.diagramHistory}
                diagramHistoryDispatch={props.diagramHistoryDispatch}
            />
        </div>
    </>
);
const ViewControls = (props: Props) => (
    <>
        <div className={[sBox, s.boxView].join(" ")}>
            <HeaderArrange
                diagram={props.diagramHistory.present}
                diagramDispatch={props.diagramHistoryDispatch}
                elements={props.elements}
            />
        </div>
        <div className={[sBox, s.boxView].join(" ")}>
            <HeaderZoom />
        </div>
        <div className={[sBox, s.boxView].join(" ")}>
            <HeaderExport diagram={props.diagramHistory.present} />
        </div>
    </>
);

interface Props extends DiagramHistoryProps {
    elements: DiagramElements;
}

export const Header = (props: Props) => {
    const [more, setMore] = React.useState(false);
    return (
        <div className={[sContainer, background.primary].join(" ")}>
            <div className={[sMain, s.row].join(" ")}>
                <div className={sBox}>
                    <HeaderLibrary />
                </div>
                <EditControls {...props} />
                <div className={[sBox, s.search].join(" ")}>
                    <HeaderSearch
                        diagram={props.diagramHistory.present}
                        diagramDispatch={props.diagramHistoryDispatch}
                        elements={props.elements}
                    />
                </div>
                <ViewControls {...props} />
                <div className={sBox}>
                    <HeaderMore more={more} setMore={setMore} />
                </div>
            </div>
            {more && (
                <>
                    <Border color="weak" />
                    <div
                        className={[sMore, s.row, sBackground.stripe].join(" ")}
                    >
                        <EditControls {...props} />
                        <ViewControls {...props} />
                        {isAppHost === false && (
                            <div className={sBox} children={<HeaderTheme />} />
                        )}
                        <div className={sBox} children={<HeaderAnimation />} />
                        <div className={sBox} children={<HeaderFooter />} />
                        <DivGrow />
                        <div className={sBox} children={<HeaderReport />} />
                    </div>
                </>
            )}
        </div>
    );
};
