import { Button, Input, MutedSpan } from "@moai/core";
import { stringifyDiagram } from "diagram/model/stringify/stringify";
import { LibraryReducer } from "library/reducer";
import { DocStorage } from "library/storage/doc";
import * as React from "react";
import { LibraryItemProps } from "../item";
import s from "./name.module.scss";

interface Props extends LibraryItemProps {
    isEdit: boolean;
    setIsEdit: React.Dispatch<React.SetStateAction<boolean>>;
}

const open = (props: Props): void => {
    if (props.isCurrent) return;
    props.setLibrary(LibraryReducer.open(props.item.name));
};

const rename = async (props: Props, to: string): Promise<void> => {
    const from = props.item.name;
    if (from === to) return;
    if (props.item.saved === false) {
        const text = stringifyDiagram(props.item.diagram, "edit", "json");
        await DocStorage.set(to, text);
    } else {
        await DocStorage.rename(from, to);
    }
    props.setLibrary(LibraryReducer.rename(from, to));
};

export const LibraryItemName = (props: Props) => {
    if (props.isEdit) {
        return (
            <Input
                style={Input.style.flat}
                defaultValue={props.item.name}
                // Don't use onKeyDown here as the Enter will also trigger the
                // auto-focused "OK" button in the confirm dialog
                onKeyUp={async (event) => {
                    if (event.key !== "Enter") return;
                    await rename(props, event.currentTarget.value);
                    props.setIsEdit(false);
                }}
                onBlur={async (event) => {
                    await rename(props, event.currentTarget.value);
                    props.setIsEdit(false);
                }}
                autoFocus
            />
        );
    } else {
        const className = [
            s.button,
            Button.style.flat.main,
            Button.size.medium.main,
            props.isCurrent ? s.current : "",
            props.item.saved === false ? s.unsaved : "",
        ].join(" ");
        return (
            <button
                className={className}
                onClick={() => void open(props)}
                onDoubleClick={() => void props.setIsEdit(true)}
                title="Click to select, Double-click to rename"
            >
                {props.item.name}
                {props.item.saved === false && (
                    <MutedSpan> - unsaved</MutedSpan>
                )}
            </button>
        );
    }
};
