import { ButtonMenuItem } from "@moai/core";
import { isAppHost } from "host/host";
import { showInstallHostAlert } from "host/install";

export const getHeaderImportSqlItems = (): ButtonMenuItem[] => {
    if (isAppHost) return [];
    const fn = (): void => {
        showInstallHostAlert("Generate from database require TablePlus app");
    };
    return [{ label: "Generate from database", fn }];
};
