import * as React from "react";
import s from "./marker.module.scss";

export const EDGE_FROM_MARKER_ID = "edge-from"
export const EDGE_TO_MARKER_ID = "edge-to"

const From = () => (
    <marker
        id={EDGE_FROM_MARKER_ID}
        viewBox="0 0 10 10"
        refX="4"
        refY="5"
        markerWidth="10"
        markerHeight="10"
        markerUnits="userSpaceOnUse"
    >
        <circle className={s.container} cx="5" cy="5" r="4" />
    </marker>
);

const To = () => (
    <marker
        id={EDGE_TO_MARKER_ID}
        viewBox="0 0 10 10"
        refX="10"
        refY="5"
        markerWidth="10"
        markerHeight="10"
        markerUnits="userSpaceOnUse"
        orient="auto-start-reverse"
    >
        <polygon className={s.container} points="1,1 9,5 1,9" />
    </marker>
);

export const EdgeCanvasMarkers = () => (
    <defs>
        <From />
        <To />
    </defs>
);
