import { Button } from "@moai/core";
import { icons } from "@moai/icon";
import { DiagramElements, FieldName } from "canvas/elements";
import React from "react";
import s from "./remove.module.css";

interface Props {
    tableId: number;
    rowId: number;
    removeRow: (name: FieldName) => void;
}

export const RowRemoveButton = (props: Props) => (
    <div className={[s.container].join(" ")}>
        <Button
            onClick={() => {
                props.removeRow(DiagramElements.removeFieldName);
            }}
            style={Button.style.flat}
            size={Button.size.small}
            icon={icons.remove}
        />
    </div>
);
