import { Menu } from "@moai/core";
import { DiagramElements } from "canvas/elements";
import { DiagramProps } from "diagram/state/state";
import { Position } from "geometry/point";
import * as React from "react";
import { copyToClipboard } from "utilities/clipboard";
import { getCanvasCopyText } from "../shorcut/copy";
import s from "./menu.module.css";

interface Props extends DiagramProps {
    elements: DiagramElements;
}

const getTables = (canvas: DiagramElements): Set<HTMLElement> => {
    const details = [...canvas.tables.values()];
    const elements = details
        .map((detail) => detail.element)
        .filter((element): element is HTMLDivElement => element !== null);
    return new Set(elements);
};

const remove = (props: Props): void => {
    props.diagramDispatch({ type: "remove-selected-items" });
};

const duplicate = (props: Props): void => {
    props.diagramDispatch({ type: "duplicate-selected-table" });
};

const copy = (props: Props): void => {
    const text = getCanvasCopyText(props.diagram);
    copyToClipboard(text);
};

export const CanvasContextMenu = (props: Props) => {
    const { elements, diagram } = props;

    const [position, setPosition] = React.useState<Position | null>(null);

    React.useEffect(() => {
        const { canvas } = elements;
        if (canvas === null) throw Error("Canvas is null");
        canvas.addEventListener("contextmenu", (event) => {
            const { target } = event;
            if (target === null) return;
            if (target instanceof HTMLInputElement) return;
            if (target instanceof HTMLTextAreaElement) return;
            if (!(target instanceof HTMLElement)) return;

            let element: HTMLElement | null = target;
            const tables = getTables(elements);
            while (element !== null) {
                if (tables.has(element)) {
                    event.preventDefault();
                    setPosition({ top: event.clientY, left: event.clientX });
                }
                element = element.parentElement;
            }
        });
        // Diagram is not used in the effect but it's necessary to create new
        // menu, mostly because of elements.tables, which is a ref
    }, [elements, diagram]);

    const wrap = (fn: Function) => () => {
        setPosition(null);
        fn();
    };

    return position ? (
        <div
            className={s.container}
            style={{ top: position.top, left: position.left }}
        >
            <Menu
                items={[
                    { label: "Copy", fn: wrap(() => copy(props)) },
                    { label: "Duplicate", fn: wrap(() => duplicate(props)) },
                    "divider",
                    { label: "Delete", fn: wrap(() => remove(props)) },
                ]}
                onEsc={() => setPosition(null)}
            />
        </div>
    ) : null;
};
